//      @font-face
//        $fonts   _partials/_variables.sass 
@mixin fonts($font-variant)
  @each $f-family, $f-ext, $f-weight, $f-style, $f-display in $font-variant
    @font-face
      font-family: $f-family
      src: local("#{$f-family} #{$f-ext}"), local("#{$f-family}-#{$f-ext}"), url("../fonts/#{$f-family}-#{$f-ext}.woff2") format("woff2"), url("../fonts/#{$f-family}-#{$f-ext}.woff") format("woff"), url("../fonts/#{$f-family}-#{$f-ext}.ttf") format("truetype")
      font-weight: $f-weight
      font-style: $f-style
      font-display: $f-display




